.main-content{
	margin-top: 80px;
}
.mb-10{
	margin-bottom: 10px;
}
.no-margin{
    margin: 0 !important;
}
.navbar a.active{
	color: #555;
    background-color: #e7e7e7;
}
.navbar-default .navbar-nav>li>a.active:focus, .navbar-default .navbar-nav>li>a.active:hover {
    color: #333;
    background-color: #e7e7e7 !important;
}
.badge-warning {
background-color: #f89406 !important;
}
.badge-warning:hover {
background-color: #c67605;
}
.badge-success {
background-color: #468847 !important;
}
.badge-success:hover {
background-color: #356635;
}
.badge-info {
background-color: #3a87ad !important;
}
.badge-info:hover {
background-color: #2d6987;
}
.react-selectize.root-node{
	width: 100% !important;
}
.badge{
	margin: 0 5px;
}
.react-datepicker-wrapper, .react-datepicker__input-container, .react-datepicker__input-container input {
    width: 100% !important;
}
input.error{
	border: 1px solid #ff0000;
}
input.error:focus {
    border-color: #ff0000 !important;
    outline: 0 !important;
    box-shadow: inset 0 1px 1px rgba(255,0,0,.075), 0 0 8px rgba(255,0,0,.6) !important;
}
